html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-size: 1rem;
}

main {
  display: grid;
  gap: 1.625rem;
}
@media (min-width: 40em) {
  main {
    place-items: center;
  }
}
@media (min-width: 71.875em) {
  main {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 8.125rem;
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);
  --very-dk-blue: hsl(220, 13%, 13%);
  --dk-gray-blue: hsl(219, 9%, 45%);
  --gray-blue: hsl(220, 14%, 75%);
  --lt-gray-blue: hsl(223, 64%, 98%);
  --black-opac: hsla(0, 0%, 0%, 75%);
}

:root {
  --font-body: "Kumbh Sans", sans-serif;
}

.container {
  max-width: 69.5rem;
  margin-inline: auto;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

.nav {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding-top: 1.25rem;
  padding-bottom: 1.5625rem;
  padding-inline: 1.5625rem;
}
@media (min-width: 71.875em) {
  .nav {
    border-bottom: 0.0625rem solid var(--gray-blue);
    margin-bottom: 5.625rem;
    padding-top: 1.75rem;
    padding-bottom: 2.1875rem;
    padding-inline: 0;
  }
}
.nav__menu {
  width: 1.125rem;
  height: 1rem;
  margin-right: 1rem;
  border: none;
  background-color: var(--white);
  background-image: url("/images/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .nav__menu {
    display: none;
  }
}
.nav__menu:focus {
  background-image: url("/images/icon-menu-black.svg");
}
.nav__logo {
  width: 8.625rem;
  height: 1.375rem;
}
@media (min-width: 71.875em) {
  .nav__logo {
    margin-right: 3.625rem;
  }
}
.nav__links {
  display: flex;
  flex-flow: row wrap;
  gap: 2.0625rem;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.nav__links--li {
  position: relative;
  font-size: 0.8125rem;
  color: var(--dk-gray-blue);
}
.nav__links--li a {
  color: var(--dk-gray-blue);
}
.nav__links--li::before {
  content: "";
  position: absolute;
  opacity: 0;
  height: 0.3125rem;
  width: 100%;
  top: 399%;
  background-color: var(--orange);
  transition: opacity 300ms ease-in-out;
}
.nav__links--li:hover::before {
  opacity: 1;
}
@media (max-width: 71.8125em) {
  .nav__links {
    display: none;
  }
}
.nav__separator {
  flex-grow: 1;
}
.nav__cart {
  width: 1.4375rem;
  height: 1.375rem;
  margin-right: 1.5rem;
  border: none;
  background-color: var(--white);
  background-image: url("/images/icon-cart.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .nav__cart {
    margin-right: 3rem;
  }
}
.nav__cart:focus {
  background-image: url("/images/icon-cart-black.svg");
}
.nav__avatar {
  position: relative;
  width: 1.5625rem;
  height: 1.5625rem;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
.nav__avatar img {
  width: 100%;
  height: 100%;
}
@media (min-width: 71.875em) {
  .nav__avatar {
    width: 3.25rem;
    height: 3.25rem;
  }
}
.nav__avatar::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  border: 0.1875rem solid var(--orange);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 300ms ease-in;
}
.nav__avatar:hover::before {
  opacity: 1;
}
.slider {
  display: grid;
  place-items: center;
}
@media (min-width: 40em) {
  .slider {
    display: none;
  }
}
.slider__poster {
  position: relative;
  width: 23.4375rem;
  height: 18.75rem;
}
.slider__poster button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  border-radius: 50%;
  background-color: var(--white);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 0.625rem 0.875rem;
  cursor: pointer;
}
.slider__poster--imgs {
  width: 23.4375rem;
  height: 18.75rem;
}
.slider__poster--imgs img {
  width: 100%;
  height: 100%;
}
.slider__poster--hide {
  display: none;
}
.slider__poster--show {
  display: block;
}
.slider__poster--prev {
  width: 2.625rem;
  height: 2.625rem;
  left: 1rem;
  background-image: url("/images/icon-previous.svg");
}
.slider__poster--next {
  width: 2.625rem;
  height: 2.625rem;
  right: 1rem;
  background-image: url("/images/icon-next.svg");
}

.product {
  margin-bottom: 5.5rem;
  padding-inline: 1.5625rem;
}
@media (min-width: 71.875em) {
  .product {
    margin-bottom: 0;
    padding-inline: 3.125rem;
  }
}
.product__company {
  margin-top: 0;
  margin-bottom: 1.25rem;
  font-size: 0.6875rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--orange);
}
@media (min-width: 71.875em) {
  .product__company {
    font-size: 0.75rem;
  }
}
.product__name {
  margin-bottom: 1.6875rem;
  font-size: 1.3125rem;
  font-weight: 700;
  color: var(--very-dk-blue);
}
@media (min-width: 71.875em) {
  .product__name {
    margin-bottom: 3.125rem;
    font-size: 2.125rem;
  }
}
.product__info {
  margin-top: 0;
  margin-bottom: 2.25rem;
  font-size: 0.875rem;
  line-height: 1.8;
  color: var(--dk-gray-blue);
}
.product__price {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5625rem;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .product__price {
    flex-flow: column wrap;
    align-items: flex-start;
    gap: 1rem;
  }
}
.product__price--new {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  gap: 1rem;
  font-size: 1.5rem;
  color: var(--very-dk-blue);
}
.product__price--discount {
  padding-block: 0.3125rem;
  padding-inline: 0.4375rem;
  font-size: 0.875rem;
  border-radius: 0.3125rem;
  background-color: var(--pale-orange);
  color: var(--orange);
}
.product__price--old {
  font-size: 0.875rem;
  text-decoration: line-through;
  color: var(--dk-gray-blue);
}
.product__add {
  display: grid;
  gap: 1.0625rem;
}
@media (min-width: 40em) {
  .product__add {
    grid-template-columns: 1fr 2fr;
  }
}
.product__add div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.625rem;
  padding-block: 1.225rem;
  padding-inline: 1.4375rem;
  background-color: var(--lt-gray-blue);
}
.product__add div button {
  width: 1.25rem;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
}
.product__add--quantity {
  font-size: 0.875rem;
  font-weight: 700;
}
.product__add--minus {
  height: 1.25rem;
  transition: opacity 300ms ease-in;
  background-image: url("/images/icon-minus.svg");
  background-size: 0.8125rem 0.3125rem;
}
.product__add--minus:hover {
  opacity: 0.5;
}
.product__add--plus {
  height: 1.25rem;
  transition: opacity 300ms ease-in;
  background-image: url("/images/icon-plus.svg");
  background-size: 0.8125rem 0.8125rem;
}
.product__add--plus:hover {
  opacity: 0.5;
}
.product__add--tocart {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.9375rem;
  width: 100%;
  border: none;
  border-radius: 0.625rem;
  padding-block: 1.1875rem;
  background-color: var(--orange);
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--white);
  transition: opacity 300ms ease-in;
  cursor: pointer;
}
.product__add--tocart:hover {
  opacity: 0.5;
}
.product__add--tocart img {
  width: 1rem;
  height: 1.125rem;
}

.gallery {
  display: none;
  width: 27.8125rem;
}
@media (min-width: 40em) {
  .gallery {
    display: block;
  }
}
.gallery__poster {
  margin-bottom: 1.875rem;
}
.gallery__poster--hide {
  display: none;
  width: 27.8125rem;
  height: 27.625rem;
  border-radius: 0.625rem;
  cursor: pointer;
}
.gallery__poster--show {
  display: block;
}
.gallery__thumbnail {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.gallery__thumbnail--img {
  width: 5.6875rem;
  height: 5.875rem;
  border-radius: 0.625rem;
  transition: opacity 300ms ease-in;
  cursor: pointer;
}
.gallery__thumbnail--img:hover {
  opacity: 0.5;
}
.gallery__thumbnail--selected {
  opacity: 0.5;
  border: 0.125rem solid var(--orange);
}